import React, { useState, useEffect } from 'react';
import GridContainer from '../../../@jumbo/components/GridContainer';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import Grid from '@material-ui/core/Grid';
import Filter from './Filter';
import Table from './Table';
import * as api from '../../../api/api';
import { CircularProgress } from '@material-ui/core';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import IntlMessages from '@jumbo/utils/IntlMessages';

const DIDReport = () => {
  const [response, setResponse] = useState([]);
  const [didNumber, setDidNumber] = useState('');
  const [count, setCount] = useState(0);

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'did.report'} />, isActive: true },
  ];

  // function checkBetween(startDate, endDate) {
  //   if (Math.round((new Date(endDate).getTime() - new Date(startDate).getTime()) / 1000 / 60 / 60 / 24) <= 30) {
  //     return true;
  //   } else return false;
  // }

  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };
  let today = new Date();
  let yesterday = new Date();
  yesterday.setDate(today.getDate() - 1);
  const [selectedStartTime, setSelectedStartTime] = useState(formatDate(yesterday) + ' 00:00:00');
  const [selectedEndTime, setSelectedEndTime] = useState(formatDate(today) + ' 23:59:00');

  const [dates, setDates] = useState({
    paramStart: formatDate(yesterday) + ' 00:00:00',
    paramFinish: formatDate(today) + ' 23:59:00',
  });
  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  let query = `with recursive cte1 as (
    select 
    *
    from cl_segments_view_new
    where 
    (date_trunc('second',  TIMESTAMP '${dates.paramStart}') <=  start_time)
    and (date_trunc('second',  TIMESTAMP '${dates.paramFinish}' + interval '1 day') >= start_time)
    and src_did != ''
  	and
	  dst_answer_time is not null
    )
	,
	cte2 as (
    select
	 sum(dst_end_time-dst_answer_time) as duration,
    src_did,	
    count(distinct(call_id)) as total_call
    from cte1 	
    -- where dst_dn_type = 0
    --  and act != 1
    group by src_did
    )
	,	
	cte3 as (
    select
    src_did,	
    count(distinct(call_id)) as did_not_asnwered
    from cte1 	
    where dst_answer_time is  null	
    and act!= 1
    group by src_did
    )
    select
    cte2.src_did as did_number,
    cte2.total_call as total_did,
    coalesce(cte3.did_not_asnwered,0) as did_not_answered,
    cte2.total_call - coalesce(cte3.did_not_asnwered,0) as answered,
	to_char(coalesce(cte2.duration,'0 second'),'HH24h MIm SSs') as duration,
	to_char((coalesce(cte2.duration/cte2.total_call,'0 second')),'HH24h MIm SSs') as act
    from
    cte2
    left outer join cte3 on cte2.src_did = cte3.src_did
    where (cte2.src_did like '%${didNumber}' or cte2.src_did like '%${didNumber}')
    limit ${limit} offset ${offset}		
`;

  let queryCount = ` select 
    count(distinct(src_did))
    from cl_segments_view_new
    where 
    (date_trunc('second',  TIMESTAMP '${dates.paramStart}') <=  start_time)
    and (date_trunc('second',  TIMESTAMP '${dates.paramFinish}' + interval '1 day') >= start_time)
    and src_did != ''`;

  let [loading, setLoading] = useState(false);
  let [notFirstReq, setNotFirstReq] = useState(false);

  useEffect(() => {
    if (notFirstReq) {
      // if (checkBetween(dates.paramStart, dates.paramFinish)) {
      if (true) {
        setLoading(true);
        api.getDidData(query).then(resp => {
          setResponse(JSON.parse(resp.data.data));
          setLoading(false);
        });

        api.getDidDataCount(queryCount).then(resp => {
          setCount(parseInt(JSON.parse(resp.data.data)[0].count));
        });
      } else {
        NotificationManager.error('Sadece 30 günlük veri çekebilirsiniz', '', 3000);
      }
    }
  }, [query, notFirstReq, didNumber, queryCount]);
  return (
    <PageContainer heading={<IntlMessages id={'did.report'} />} breadcrumbs={breadcrumbs}>
      <NotificationContainer />
      <GridContainer>
        <Grid item xs={12}>
          <Filter
            didNumber={didNumber}
            setDidNumber={setDidNumber}
            selectedStartTime={selectedStartTime}
            selectedEndTime={selectedEndTime}
            setSelectedEndTime={setSelectedEndTime}
            setSelectedStartTime={setSelectedStartTime}
            setParams={setDates}
            setOffset={setOffset}
            setPage={setPage}
            setLimit={setLimit}
            setNotFirstReq={setNotFirstReq}
            notFirstReq={notFirstReq}
          />
        </Grid>
        <Grid item xs={12}>
          {loading && (
            <div style={{ textAlign: 'center', marginTop: '20px' }}>
              <CircularProgress />
            </div>
          )}
        </Grid>
        <Grid item xs={12}>
          <Table
            page={page}
            setPage={setPage}
            setLimit={setLimit}
            limit={limit}
            setOffset={setOffset}
            offset={offset}
            response={response}
            count={count}
            setResponse={setResponse}
          />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
};
export default DIDReport;
