import React, { useState, useEffect } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import { CircularProgress, FormControl, InputLabel, MenuItem, Select } from '@material-ui/core';
import {
  getAbandonedQueueCalls,
  getAbandonedQueueCallsDetails,
  getQueue,
  getAbandonedQueueCallsDetailsTotal,
  getAgentNumber
} from 'api/api';
import GridContainer from '@jumbo/components/GridContainer';
import { Formik, Form } from 'formik';
import { Grid } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import AbondenedQueuesDateFilter from './DateFilter';

function Filter({
  setResponse,
  limit,
  offset,
  setQueueList,
  queuelist,
  setDetails,
  setPage,
  setOffset,
  setDetailsTotal,
  setResponseCount,
}) {
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };
  const useStyles = makeStyles(theme => ({
    root: {
      margin: '0 auto',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));
  const classes = useStyles();
  const today = new Date();
  const yesterday = new Date();
  yesterday.setDate(today.getDate() - 1);
  const [startDate, setStartDate] = useState(formatDate(today));
  const [endDate, setEndDate] = useState(formatDate(today));
  const timeOffset = localStorage.getItem('timeoffset') || '+3';
  const [params, setParams] = useState({ startDate: formatDate(today), endDate: formatDate(today) });
  const [queue, setQueue] = useState('');
  let string = `SELECT callcent_ag_dropped_calls.q_num, callcent_ag_dropped_calls.ag_num, to_char(callcent_ag_dropped_calls.time_start at time zone 'utc${timeOffset}','dd-mm-yyyy HH24:MI:SS') as time_start, to_char(callcent_ag_dropped_calls.time_end at time zone 'utc${timeOffset}','dd-mm-yyyy HH24:MI:SS') as time_end,callcent_ag_dropped_calls.q_call_history_id,callcent_queuecalls.from_userpart, callcent_queuecalls.from_displayname, callcent_ag_dropped_calls.reason_noanswerdesc,callcent_ag_dropped_calls.time_end - callcent_ag_dropped_calls.time_start as waiting FROM callcent_ag_dropped_calls inner join callcent_queuecalls on callcent_ag_dropped_calls.q_call_history_id = callcent_queuecalls.call_history_id where  callcent_ag_dropped_calls.time_start >= '
	${params.startDate}' 
	 and callcent_ag_dropped_calls.time_start <= timestamp '${params.endDate}' + interval '1 day' 
	and callcent_ag_dropped_calls.reason_noanswerdesc = 'Cancelled' ${
    queue === '' ? '' : "and callcent_ag_dropped_calls.q_num = '" + queue + "'"
  } limit ${limit} offset ${offset}`;

  let [loading, setLoading] = useState(true);

  let stringCount = `SELECT 
  count(*)
  FROM callcent_ag_dropped_calls 
  inner join callcent_queuecalls on callcent_ag_dropped_calls.q_call_history_id = callcent_queuecalls.call_history_id
  where  callcent_ag_dropped_calls.time_start >= '${params.startDate}' 
  and
  callcent_ag_dropped_calls.time_start <= timestamp '${params.endDate}' + interval '1 day' 
  and 
  callcent_ag_dropped_calls.reason_noanswerdesc = 'Cancelled'
  and callcent_ag_dropped_calls.q_num like '%${queue}'
  `;

  useEffect(() => {
    setLoading(true);
    getAbandonedQueueCalls(string).then(response => {
      getAgentNumber().then(resp => {
        var list = JSON.parse(response.data.data);
        for (let i = 0; i < list.length; i++) {
          JSON.parse(resp.data.data).map(res => {
            if (list[i].ag_num === res.dn) {
              list[i].ag_num = res.dn + '-' + res.display_name;
            }
            return null;
          });
        }
        setResponse(list);
        setLoading(false);
      });
    });

    getAbandonedQueueCalls(stringCount).then(response => {
      setResponseCount(JSON.parse(response.data.data)[0].count);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [string]);
  useEffect(() => {
    getQueue().then(response => {
      setQueueList(JSON.parse(response.data.data));
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    getAbandonedQueueCallsDetails(params, queue).then(response => {
      setDetails(JSON.parse(response.data.data));
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [params, queue]);

  useEffect(() => {
    getAbandonedQueueCallsDetailsTotal(params, queue).then(response => {
      setDetailsTotal(JSON.parse(response.data.data));
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [params, queue]);

  const handleClick = () => {
    setParams({ startDate: formatDate(startDate), endDate: formatDate(endDate) });
    setOffset(0);
    setPage(0);
  };

  const handleChangeQueue = event => {
    event.target.value === '' ? setQueue('') : setQueue(event.target.value);
  };
  return (
    <div>
      <div style={{ marginBottom: '10px' }}>
        <AbondenedQueuesDateFilter setStartTimeApi={setStartDate} setEndTimeApi={setEndDate} />
      </div>
      <Card className={classes.root}>
        <Formik
          initialValues={{
            queueCheck: '',
          }}
          onSubmit={values => {
            setQueue(values.queueCheck);
          }}>
          {({ values, handleChange, handleSubmit }) => (
            <>
              <Form onSubmit={handleSubmit}>
                <CardContent>
                  <GridContainer>
                    {/* <Grid item md={3}>
                      <DatePicker
                        ampm={false}
                        disableFuture
                        inputVariant="outlined"
                        format="DD-MM-YYYY"
                        value={formatDate(startDate)}
                        onChange={setStartDate}
                        label={<IntlMessages id={'filter.menu.startdate'} />}
                      />
                    </Grid>
                    <Grid item md={3}>
                      <DatePicker
                        ampm={false}
                        disableFuture
                        inputVariant="outlined"
                        format="DD-MM-YYYY"
                        value={formatDate(endDate)}
                        onChange={setEndDate}
                        label={<IntlMessages id={'filter.menu.enddate'} />}
                      />
                    </Grid> */}
                    <Grid item md={3} xs={3}>
                      <FormControl fullWidth variant="outlined">
                        <InputLabel id="agent-select-label">{<IntlMessages id={'queue.answer.report.queues'} />}</InputLabel>
                        <Select
                          labelId="agent-select-label"
                          id="agent-select"
                          value={queue}
                          onChange={handleChangeQueue}
                          label={<IntlMessages id={'queue.answer.report.queues'} />}>
                          <MenuItem value={''}>All</MenuItem>
                          {queuelist.map((que, index) => (
                            <MenuItem key={index} value={que.dn}>
                              {que.dn} - {que.display_name}
                            </MenuItem>
                          ))}
                        </Select>
                      </FormControl>
                    </Grid>
                  </GridContainer>
                </CardContent>
                <CardActions>
                  <Button
                    onClick={() => {
                      handleClick();
                    }}
                    size="small"
                    variant="contained">
                    <IntlMessages id={'filter.menu.search'} />
                  </Button>
                </CardActions>
              </Form>
            </>
          )}
        </Formik>
      </Card>
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}

export default Filter;
