  //     getRecordingBytes(path, dir)
  //     .then(resp => resp.blob())
  //     .then(blob => {
  //       var reader = new FileReader();
  //       reader.readAsDataURL(blob);
  //       reader.onloadend = function(){
  //         //setBase64(reader.result);
  //       }
  //     })
