import React, { useEffect, useState } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import GridContainer from '@jumbo/components/GridContainer';
import { FormControl, Grid, InputLabel, MenuItem, Select } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import * as api from '../../../../api/api';
import DidReportDateFilter from './DateFilter';

export default function Filter({
  setSelectedStartTime,
  selectedStartTime,
  selectedEndTime,
  setSelectedEndTime,
  setParams,
  setLimit,
  setPage,
  setOffset,
  notFirstReq,
  setNotFirstReq,
  setDidNumber,
  didNumber,
}) {
  const useStyles = makeStyles(theme => ({
    root: {
      margin: '0 auto',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  }));
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':00';
  };
  const [didNumbers, setDidNumbers] = useState([]);
  const classes = useStyles();
  const handleClick = () => {
    setOffset(0);
    setLimit(10);
    setPage(0);
    setParams({
      paramStart: formatDate(selectedStartTime),
      paramFinish: formatDate(selectedEndTime),
    });
    if (!notFirstReq) {
      setNotFirstReq(true);
    }
  };

  useEffect(() => {
    api.getDidNumbers().then(resp => {
      setDidNumbers(JSON.parse(resp.data.data));
    });
  }, []);

  const handleChange = event => {
    setDidNumber(event.target.value);
  };

  return (
    <div>
      <div style={{ marginBottom: '10px' }}>
        <DidReportDateFilter setStartTimeApi={setSelectedStartTime} setEndTimeApi={setSelectedEndTime} />
      </div>
      <Card className={classes.root}>
        <CardContent>
          <GridContainer>
            <Grid item md={4}>
              <FormControl fullWidth variant="outlined">
                <InputLabel id="select-did-numbers">
                  <IntlMessages id={'mountlydetails.didnumber'} />
                </InputLabel>
                <Select
                  labelId="select-did-numbers"
                  id="demo-simple"
                  label={<IntlMessages id={'mountlydetails.didnumber'} />}
                  value={didNumber}
                  onChange={handleChange}>
                  <MenuItem value="">
                    <em>
                      <IntlMessages id={'all'} />
                    </em>
                  </MenuItem>
                  {didNumbers.map((did, index) => (
                    <MenuItem key={index} value={did.conditiondata}>
                      {did.conditiondata}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </CardContent>
        <CardActions>
          <Button
            onClick={() => {
              handleClick();
            }}
            size="small"
            variant="contained">
            <IntlMessages id={'filter.menu.search'} />
          </Button>
        </CardActions>
      </Card>
    </div>
  );
}
