import React, { useState, useEffect } from 'react';
import {
  Card,
  CardContent,
  CircularProgress,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from '@material-ui/core';
import AbondenedCallsDateFilter from './DateFilter';
import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { getQueue } from 'api/api';

function Filter({ setParams, setColor, color, queue, setQueue }) {
  let [loading] = useState(false);

  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };

  const today = new Date();
  const yesterday = new Date();
  yesterday.setDate(today.getDate() - 1);
  const [startDate, setStartDate] = useState(formatDate(today));
  const [endDate, setEndDate] = useState(formatDate(today));

  const handleClick = () => {
    setParams({ startDate: formatDate(startDate), endDate: formatDate(endDate) });
  };

  const colors = [
    {
      name: 'Green',
      value: <IntlMessages id={'returned'} />,
    },
    {
      name: 'Red',
      value: <IntlMessages id={'notreturned'} />,
    },
  ];

  const handleChangeColor = event => {
    setColor(event.target.value);
  };

  const handleChangeQueue = event => {
    setQueue(event.target.value);
  };

  const [queues, setQueues] = useState([]);

  useEffect(() => {
    getQueue().then(resp => {
      setQueues(JSON.parse(resp.data.data));
    });
  }, []);

  return (
    <>
      {' '}
      <div>
        <AbondenedCallsDateFilter handleClick={handleClick} setStartTimeApi={setStartDate} setEndTimeApi={setEndDate} />
        {loading && (
          <div style={{ textAlign: 'center', marginTop: '20px' }}>
            <CircularProgress />
          </div>
        )}
      </div>
      <Card>
        <CardContent>
          <GridContainer>
            <Grid item md={4}>
              <FormControl fullWidth variant="outlined">
                <InputLabel id="agent-select-label">
                  <IntlMessages id={'returnfilter'} />
                </InputLabel>
                <Select
                  labelId="agent-select-label"
                  id="agent-select"
                  value={color}
                  onChange={handleChangeColor}
                  label={<IntlMessages id={'returnfilter'} />}>
                  <MenuItem value={''}>
                    <IntlMessages id={'all'} />
                  </MenuItem>
                  {colors.map((color, index) => (
                    <MenuItem key={index} value={color.name}>
                      {color.value}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={4}>
              <FormControl fullWidth variant="outlined">
                <InputLabel id="agent-select-label">
                  <IntlMessages id={'queue'} />
                </InputLabel>
                <Select
                  labelId="agent-select-label"
                  id="agent-select"
                  value={queue}
                  onChange={handleChangeQueue}
                  label={<IntlMessages id={'queue'} />}>
                  <MenuItem value={''}>
                    <IntlMessages id={'all'} />
                  </MenuItem>
                  {queues.map((queue, index) => (
                    <MenuItem key={index} value={queue.display_name}>
                      {queue.display_name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </CardContent>
      </Card>
    </>
  );
}

export default Filter;
