import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { getNewDate } from '@jumbo/utils/dateHelper';

function Details({ details, detailsTotal }) {
  const columns = [
    { id: 'date_day', label: <IntlMessages id={'filter.menu.date'} />, minWidth: 100, align: 'center' },
    { id: 'cnt', label: <IntlMessages id={'wallboard.total.call'} />, minWidth: 170, align: 'center' },

    {
      id: 'total_waiting_time',
      label: <IntlMessages id={'waiting.time'} />,
      minWidth: 170,
      align: 'center',
    },
    {
      id: 'act',
      label: <IntlMessages id={'average'} />,
      minWidth: 170,
      align: 'center',
    },
  ];
  // const formatDate = date => {
  //   let d = new Date(date);
  //   let month = '' + (d.getMonth() + 1);
  //   let day = '' + d.getDate();
  //   let year = d.getFullYear();

  //   if (month < 10) month = '0' + month;
  //   if (day < 10) day = '0' + day;

  //   return year + '-' + month + '-' + day;
  // };
  // const returnTime = time => {
  //   if (typeof time === 'undefined') {
  //     time = '00';
  //   } else {
  //     if (time < 10) {
  //       time = '0' + time;
  //     } else {
  //       time = time;
  //     }
  //   }
  //   return time;
  // };
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));
  function createData(cnt, date_day, total_waiting_time, act) {
    return { cnt, date_day, total_waiting_time, act };
  }

  const rows = details.map(data =>
    createData(
      data.cnt,
      data.day_abandoned,
      data.total_waiting.replace(':', 'h ').replace(':', 'm ') + 's',
      data.act.replace(':', 'h ').replace(':', 'm ') + 's',
    ),
  );

  const classes = useStyles();
  return (
    <TableContainer component={Paper} className={classes.root}>
      <Table className={classes.table} aria-label="caption table">
        <caption>
          <IntlMessages id={'summery'} />
        </caption>
        <TableHead>
          <TableRow>
            {columns.map(column => (
              <TableCell key={column.id} align={column.align} style={{ minWidth: column.minWidth }}>
                {column.label}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row, index) => (
            <TableRow key={index}>
              <TableCell align="center">{getNewDate(row.date_day)}</TableCell>
              <TableCell align="center">{row.cnt}</TableCell>
              <TableCell align="center">{row.total_waiting_time.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
              <TableCell align="center">{row.act}</TableCell>
            </TableRow>
          ))}
          {detailsTotal.map((row, index) => (
            <TableRow key={index} style={{ backgroundColor: '#80ED99' }}>
              <TableCell align="center">
                <IntlMessages id={'total'} />
              </TableCell>
              <TableCell align="center">{row.totalcount}</TableCell>
              <TableCell align="center">{row.totalwaiting.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
              <TableCell align="center">{row.act.replace(':', 'h ').replace(':', 'm ') + 's'}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>{' '}
    </TableContainer>
  );
}

export default Details;
/**  */
