import React from 'react';
import Paper from '@material-ui/core/Paper';
import TablePagination from '@material-ui/core/TablePagination';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { Button } from '@material-ui/core';
import { CloudDownload } from '@material-ui/icons';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';
import { DataGrid } from '@mui/x-data-grid';
import DateRangeIcon from '@mui/icons-material/DateRange';
import PersonIcon from '@mui/icons-material/Person';
import ContactPhoneIcon from '@mui/icons-material/ContactPhone';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import { CircularProgress } from '@mui/material';
import { localeText } from '@jumbo/utils/DataGridLocales';

function DataTable({ datalist, setResponse, setPage, setLimit, setOffset, limit, offset, page, queues, responseCount }) {
  // const columns = [
  //   { id: 'call_time', label: <IntlMessages id={'filter.menu.startdate'} />, minWidth: 60, align: 'center' },
  //   {
  //     id: 'callerid',
  //     label: <IntlMessages id={'caller.id'} />,
  //     minWidth: 70,
  //     align: 'center',
  //   },
  //   { id: 'queue', label: <IntlMessages id={'queue'} />, minWidth: 60, align: 'center' },
  //   {
  //     id: 'waiting_time',
  //     label: <IntlMessages id={'waiting.time'} />,
  //     minWidth: 60,
  //     align: 'center',
  //   },
  //   {
  //     id: 'agent',
  //     label: <IntlMessages id={'agent.number'} />,
  //     minWidth: 60,
  //     align: 'center',
  //   },
  // ];
  const columns = [
    {
      field: 'num',
      headerName: '#',
      renderCell: params => <b>{params.value}</b>,
    },
    {
      field: 'call_time',
      headerName: <IntlMessages id={'filter.menu.startdate'} />,
      width: 250,
      renderCell: params => (
        <span>
          <DateRangeIcon style={{ verticalAlign: '-5px', color: '#30475E' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'callerid',
      headerName: <IntlMessages id={'caller.id'} />,
      width: 250,
      renderCell: params => (
        <span>
          <PersonIcon style={{ verticalAlign: '-5px', color: '#6998AB' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'queue',
      headerName: <IntlMessages id={'queue'} />,
      width: 250,
      renderCell: params => (
        <span>
          <ContactPhoneIcon style={{ verticalAlign: '-5px', color: '#519259' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'agent',
      headerName: <IntlMessages id={'agent.number'} />,
      width: 250,
      renderCell: params => (
        <span>
          <PersonIcon style={{ verticalAlign: '-5px', color: '#AE431E' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'waiting_time',
      headerName: <IntlMessages id={'waiting.time'} />,
      width: 250,
      renderCell: params => (
        <span>
          <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#DB6B97' }} />{' '}
          <b style={{ color: '#DB6B97' }}>{params.value}</b>
        </span>
      ),
    },
  ];

  function createData(num, id, queue, call_time, waiting_time, callerid, agent) {
    return { num, id, queue, call_time, waiting_time, callerid, agent };
  }

  for (var i = 0; i < datalist.length; i++) {
    for (var z = 0; z < queues.length; z++) {
      if (datalist[i].q_num === queues[z].dn) {
        datalist[i].q_num = queues[z].dn + '  ' + queues[z].display_name;
      }
    }
  }

  const rows = datalist.map((data, index) =>
    createData(
      index + 1 + offset,
      index,
      data.q_num,
      data.time_start,
      data.waiting
        .toString()
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
      data.from_displayname,
      data.ag_num,
    ),
  );

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setLimit(+event.target.value);
    setOffset(0);
    setPage(0);
  };
  let curentLanguage = useIntl();

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleDowlandButtonClick = () => {
    setLimit(responseCount);
    setPage(0);
    setOffset(0);
    setResponse([]);
    handleOpen();
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        {rows.length > 0 && (
          <Button onClick={() => handleDowlandButtonClick()} style={{ backgroundColor: '#3f51b5', color: 'white' }}>
            <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
          </Button>
        )}
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description">
          <Box sx={style}>
            {rows.length < 1 && (
              <div style={{ textAlign: 'center' }}>
                <CircularProgress />
                <p>Veriler hazirlanıyor...</p>
              </div>
            )}
            <div style={{ textAlign: 'center' }}>
              {rows.length < 1 && (
                <Button disabled style={{ backgroundColor: 'gray', color: 'white' }}>
                  <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                </Button>
              )}
              {rows.length > 0 && (
                <ExcelFile
                  filename={
                    curentLanguage.locale === 'en-EN'
                      ? 'Abondened_queues'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'Kuyruk_terk_raporu'
                      : curentLanguage.locale === 'de-DE'
                      ? 'Verlassene Warteschlangen'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'Verlaten wachtrijen'
                      : ' '
                  }
                  element={
                    <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                      <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                    </Button>
                  }>
                  <ExcelSheet data={rows} name="Data">
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Start Time'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Başlangıç Saati'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Starttijd'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Startzeit'
                          : ''
                      }
                      value="start_time"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Caller Number'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Arayan Numara'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Bellernummer'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Anrufernummer'
                          : ''
                      }
                      value="caller_number"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Queue'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Sıra'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Wachtrij'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Warteschlange'
                          : ''
                      }
                      value="queue"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Agent Number'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Agent Numarası'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Agent nummer'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Agentennummer'
                          : ''
                      }
                      value="agent_number"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Waiting Time'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Bekleme Süresi'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Wachttijd'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Wartezeit'
                          : ''
                      }
                      value="waiting_time"
                    />
                  </ExcelSheet>
                </ExcelFile>
              )}
            </div>
          </Box>
        </Modal>
        {/* {rows.length > 0 && (
          <ExcelFile
            filename={curentLanguage.locale === 'en-US' ? 'Abondened_queues' : 'Kuyruk_terk_raporu'}
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={rows} name="Data">
              <ExcelColumn label={curentLanguage.locale == 'en-US' ? 'Start Time' : 'Başlangıç Zamanı'} value="call_time" />
              <ExcelColumn label={curentLanguage.locale == 'en-US' ? 'Caller Number' : 'Arayan Numara'} value="callerid" />
              <ExcelColumn label={curentLanguage.locale == 'en-US' ? 'Queue' : 'Kuyruk'} value="queue" />
              <ExcelColumn label={curentLanguage.locale == 'en-US' ? 'Agent Number' : 'Agent Numarası'} value="agent" />
              <ExcelColumn
                label={curentLanguage.locale == 'en-US' ? 'Waiting Time' : 'Bekleme Süresi'}
                value="waiting_time"
              />
            </ExcelSheet>
          </ExcelFile>
        )} */}
      </div>
      <Paper>
        {/* <TableContainer className={classes.container}>
          <Table stickyHeader aria-label="sticky table" size="small">
            <TableHead>
              <TableRow>
                {columns.map(column => (
                  <TableCell key={column.id} align={column.align} style={{ minWidth: column.minWidth }}>
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row, index) => {
                return (
                  <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                    {columns.map(column => {
                      const value = row[column.id];
                      return (
                        <TableCell key={column.id} align={column.align}>
                          {value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer> */}
        <div style={{ height: 400, width: '100%' }}>
          <DataGrid rows={rows} columns={columns} hideFooter localeText={localeText} />
        </div>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={parseInt(responseCount)}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={datalist.length < limit || datalist.length === 0 ? { disabled: true } : { disabled: false }}
          rowsPerPage={limit}
          page={page}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
    </>
  );
}

export default DataTable;
